import React, {Component, PropTypes}  from "react";
import {render} from "react-dom";
import {Link} from 'react-router'
import {selectTab,fetchTopics,recordScrollT} from '../../actions/actions'
import {connect} from 'react-redux'
import Header from "../Common/Header/Header"
import Lists from "../Common/Lists/Lists"
class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.displayName = "HomePage";
        this.state = {
            isFreshing:false,
        };
        this.tabs = [
            {
                title: '全部',
                filter: 'all',
            },
            {
                title: '精华',
                filter: 'good',
            },
            {
                title: '分享',
                filter: 'share',
            },
            {
                title: '问答',
                filter: 'ask',
            },
            {
                title: '招聘',
                filter: 'job',
            }
        ];
        this.handleClick = (tab) => {
            const {selectedTab,dispatch} = this.props;
            dispatch(selectTab(tab));
        }
    }

    componentWillReceiveProps(newProps){
        const {topics,selectedTab,isFetching,dispatch,scrollT} = newProps;
        // fetchTopics开始后会先发送一个request的action,这个ation也会改变state从而触发该方法。如果不对isFetching进行判断，会再次进行fetchTopics从而进行了不必要的重复数据请求
        if(!isFetching && topics.length === 0){
            dispatch(fetchTopics(selectedTab));
        }

    }

    render() {
        const {selectedTab,dispatch,topics,isFetching}=this.props;
        return (
            <div>
                <Header tabs={this.tabs} filter={selectedTab} onClick={this.handleClick} >
                    {this.tabs.map((tab, index) =>
                    <div  key={index}>
                        {tab.filter === selectedTab && <div style={{opacity: (!isFetching || page >= 1) ? 1 : 0 }}>
                            <Lists topics={topics} dispatch={dispatch}  isFetching={isFetching}/>
                        </div>}
                    </div>
                    )}
                </Header>
            </div>
        )
    }
    componentDidMount(){
        const {selectedTab,dispatch} = this.props;

        dispatch(fetchTopics(selectedTab));

    }
}

function mapStateToProps(state) {
    const {homePage} = state;
    const {selectedTab,tabData} = homePage;
    const {isFetching,page,scrollT,topics} = tabData[selectedTab] || {isFetching:false,page:0,scrollT:0,topics:[]};

    // 当组件第一次render时,还未进行任何action,初始化的state里没有tabData[selectedTab]，所以这里需要初始化
    return {selectedTab,tabData,isFetching,page,scrollT,topics}
}
export default connect(mapStateToProps) (HomePage)



